@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background-color: v.$verde;//"background-color define el color de fondo(como su nombre lo dice)
    @include m.tablet {
        &.fijo{
            left: 0;
            top: 0;//No se ve el salto cuando aparece el header
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem rgba(17, 1, 1, 0.703) ;
        }
     }
    h1{
        color: v.$blanco;//"color" selecciona el elemento del texto  y lo cambia
        text-align: center;
        @include m.tablet {
            margin: 0;  
         }//Lo que hace es que cuando sea menor a 720px deja un espacio entre los enlaces y el titulo
    }
    .contenido-header{  
        padding: 2rem;
        @include m.tablet {
            display: flex; //"flex" indica la capacidad de un elemento flexible para alterar sus dimensiones y llenar el espacio disponible
            justify-content: space-between;//"justify-content: space-between;" distribuye uniformemente el primer contenido al inicio y el último al final
            align-items: center; //Agrupa los elementos alrededor del centro
         }
    } 
}

.navegacion-principal{
     @include m.tablet {
         display: flex;
         gap: 2rem;// añade la separación automaticamente
      }
      @include m.desktop {
          text-align: right;
          margin: 0;
          display: flex;
          gap: 2rem;
       }
    a{
        display: block;//Los enlaces se queden de arriba hacia abajo
        text-align: center;
        color:v.$blanco;
        font-size: 2.4rem;
        margin-bottom: 1rem;//"margin-bottom sirve para dejar un espacio entre cada linea.
        @include m.tablet {
            margin: 0;
         }
        &:hover{
            color: v.$amarillo;
        }//El "&" elimina el espacio entre "a" y el "hover"     
    }
    
}

