//los que contengan "@" se les conoce como directivas
@use 'variables' as v;

@mixin telefono{
     @media (min-width: v.$telefono){
         @content;
     }
}

@mixin tablet{
    @media (min-width: v.$tablet){
        @content;
    }
}

@mixin desktop{
    @media (min-width: v.$desktop){
        @content;
    }
}
@mixin desktopXL{
    @media (min-width: v.$desktopXL){
        @content;
    }
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;//separación entre cada columna

}

@mixin resetear-lista {
    padding: 0;
    margin: 0;
    list-style: none;
}




