@use 'base/mixins' as m;
@use 'base/variables' as v;

.lineup{
    background-color: v.$rosa;
    padding: 5rem 0;//toma el espacio entre el color rosa y el titulo LineUp 

    h3, p {
        text-align: center;
        color: v.$blanco;
    }

    .dia{
        text-transform: uppercase;
        margin-bottom: 5rem;
    }
}

.escenarios-contenedor{
  @include m.tablet {
    @include m.grid(2, 5rem);
   }  

   p{
       color: v.$negro;
   }
   
}

.escenario{
    padding-top: 2rem;//padding-top es para la parte de arriba 

    p{

         &:after{//esto se considera pseudoelemento
         content: '';//se tiene que agregar el "content" siempre que se agrega el pseudoelemento
         display: block;
         width: 20rem;
         height: 10rem;
         background-size: 15rem;//especifica el tamaño de las imágenes de fondo
         background-repeat: no-repeat;
         background-position: center center;
         margin: 2rem auto 0 auto; 
         }//Todo este código sirve para agregar el icono abajo del nombre del escenario
    }

}

.rap .nombre-escenario:after{ 
    background-image: url(../img/icon_HipHop.png);
}

.rock .nombre-escenario:after{ 
    background-image: url(../img/icono_rock.png);
}

.calendario{
    @include m.resetear-lista;
    
    li{
        padding: 2.5rem;//espacio entre cada linea
        font-weight: 700;//grosor de la letra
        span{
            font-weight: 400;//grosor de la letra
        }
    }
}
.bg-amarillo{
    background-color: v.$amarillo;

    li:nth-child(odd) {
        background-color: darken(v.$amarillo, 15%);
    }
}

.bg-verde{
    background-color: v.$verde;

    li:nth-child(odd) {
        background-color: darken(v.$verde, 10%);
    }//"nth-child" selecciona una serie de elementos hijos //"odd" son para seleccionar los impares, "even" son para los pares
}
