@use 'base/variables' as v;
@use 'base/mixins' as m;

.video{
    position: relative;
    max-height: 70rem;//altura máxima
    min-height: 50rem;
    overflow: hidden;//"overflow" permite regular la visiblidad del contenido. "hidden" hace que el texto que sobresalga por encima y sea visible.

    .overlay{
        position: absolute;
        background: rgb(240,0,255);
        background: linear-gradient(270deg, rgba(240,0,255,0.3) 0%, rgba(119,2,44,0.5866209262435678) 100%);
        height: 100%;
        width: 100%;
        display: flex;//nos ayuda a tener filas y columnas
        flex-direction: column;//ocupamos columnas
        justify-content: center;//centramos el contenido del flex
    }

    video{
        width: auto;
        display: block;//con esto el video toma la parte de abajo
        @include m.desktopXL {
            width: 100%;//Nos ayuda a tomar toda la parte de la pantalla
         }
    }
}

.contenido-video{
    z-index: 1 ;//"z-index"indica el orden de un elemento posicionado y sus descendientes
    h2{
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
    }

    p{
        background-color: v.$rosa;
        display: inline-block;//solo cubre la parte requerida
        padding: 1rem 2rem;//"1rem" toma la parte de arriba y abajo y el de "2rem" de derecha a izquierda 
        color: v.$blanco;
        font-weight: 700;
        font-size: 2.4rem;//"font-size" define el tamaño de la letra
        margin: 0;
    }
}