@use 'variables' as v;
html {
    font-size: 62.5%;//"font-size" especifica el tamaño de la letra esto servira para que un 1rem equivalga a px
    box-sizing: border-box;//"box-sizing" ayuda a calcular las medidas de un elemento and "border-box" hace que el padding y el border tomen el calculo de la caja(documento) 
}
*,
*:before,
*:after{
    box-sizing: inherit;
}/**Del html hasta esta llave es necesario para poder tener una mejor forma del cuerpo de la pagina*/
body{
    font-family: v.$fuente_principal;//"font-family" define una lista de fuentes(tipo de letra)
    font-size: 1.6rem;//"font-size" define el tamaño de letra 

    &.fijar-body{
        overflow: hidden;
    }//con "overflow: hidden;" no permite dar scroll cuando se abre la imagen 
    &.body-scroll{
        padding-top: 8.6rem;
    }//se le agrega el mismo tamaño para que no se vea el salto cuando aparece el header
}

p{
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.5;
}
.contenedor{
    width: 95%;//""width" especifica la anchura del area del contenido de un elemento
    max-width: 120rem;//"max-width" especifica el máximo ancho de un elemento
    margin: 0 auto;
}
a{
    text-decoration: none;
}
h1, h2, h3 {
    margin: 0 0 5rem 0; //primer "0" es el margen de arriba, segundo "0" margen derecha, el 3er valor "5rem" abajo y el 4to "0" izquierda, velo como las manecillas del reloj los valores. 
}
h1{
    font-size: 4rem;
}//"font-sise" define el tamaño de letra
h2{
    font-size: 3.6rem;
}//"font-sise" define el tamaño de letra
h3{
    font-size: 3rem;
}//"font-sise" define el tamaño de letra
img{
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}
//Para que la imagen se pueda ver bien se tiene que utilizar "max-width" and "width" 