@use 'base/mixins' as m;
@use 'base/variables' as v;

.galeria {
    padding: 5rem 0;
    
    
    h3{
        text-align: center;
    }
}

.galeria-imagenes {
    @include m.resetear-lista;
    @include m.grid(2, 2rem);

    @include m.telefono {
        @include m.grid(3, 2rem);
     }
    @include m.tablet {
        @include m.grid(4, 2rem);
     }
    @include m.desktop {
        @include m.grid(6, 2rem);
     }
     @include m.desktopXL {
        @include m.grid(6, 2rem);
     }
     
}

.overlay {
        position: fixed;//fija la imagen en el html
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        //centramos el contenido de las imagenes en grandes
        
        width: 100%;
        height: 100%;

        display: grid;//agregamos el grid para utilizar "place-items"
        place-items: center;// con esta función centra la imagen 
        background-color: rgb(0 0 0 /.5);// la imagen toma una transparece de fondo del 50%
        picture {
        display: inline-block;
        max-width: 80rem;
        }//"display" cambia el modo en el que se visualiza cualquier elemente html y "block" hace que el elemento sea como un bloque
    
    
}

.btn-cerrar{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    display: grid;//utilizamos la propiedad grid para poder centar la "X"
    place-items: center;//"place-items" nos permite centar el elemento horizontal y verticalmente
    font-weight: 700;

    &:hover {
        cursor: pointer;
    }//La propiedad ":hover" sirve para dar estilo al pasar con el cursor encima de un elemento
    @include m.desktopXL{
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
        background-color: v.$blanco;
        color: v.$negro;
        display: grid;//utilizamos la propiedad grid para poder centar la "X"
        place-items: center;//"place-items" nos permite centar el elemento horizontal y verticalmente
        font-weight: 700;
    }
}